<template>
    <div class="main" ref="main">
        <div class="header">
            <span class="i" @click="$router.back()">
                <van-icon name="arrow-left" />
            </span>
            <span>{{ $route.params.filmName }}</span>
            <span><van-icon name="search" size="22" color="black" /></span>
        </div>

        <van-tabs swipeable v-if="showCinemas.length" title-active-color="green">

            <van-tab v-for="(cinemas, index) in dateList" :title="cinemas" :key="index">
                <CinemaList :name="cinemas" :isInclude="showCinemas[index]" :height=getCinemaListHeight()>
                    <div>
                        <div class="tab">全城<van-icon name="arrow-down" size='10px' /></div>
                        <div class="tab">最近去过<van-icon name="arrow-down" size='10px' /></div>
                    </div>
                </CinemaList>
            </van-tab>
        </van-tabs>
    </div>
</template>
<script>
import http from '@/util/http.js'
import CinemaList from "@/components/CinemaList"
import moment from 'moment'
import getTime from "@/util/getTime.js"
import { Tabs, Tab, Icon } from "vant"

export default {
    components: {
        [Tabs.name]: Tabs,
        [Tab.name]: Tab,
        [Icon.name]: Icon,
        CinemaList
    },
    data() {
        return {
            showCinemas: [],
            dateList: [],
        }
    },
    mounted() {
        http({
            url: `/gateway/?filmId=${this.$route.params.myID}&cityId=${this.$store.state.cityId}&k=1035572`,
            headers: { "X-Host": "mall.film-ticket.cinema.film-show-cinema" },
        }).then(res => {

            this.showCinemas = res.data.data.showCinemas.sort((a, b) => a.showDate - b.showDate)
            this.dateList = this.Calculation()
            // console.log(this.Calculation())
        })

    },
    methods: {
        getCinemaListHeight() {
            const html = document.documentElement
            const htmlSize = parseInt(getComputedStyle(html).fontSize)
            return this.height = window.innerHeight - 3.0625 * htmlSize - 2.875 * htmlSize + 'px'
        },
        Calculation() {
            return this.showCinemas.map(data => getTime(data.showDate * 1000, ["week", "month", "day"]))
        }
    },
}
</script>
<style lang="scss" scoped>
.header {
    display: flex;
    padding: 0 .625rem;
    box-sizing: border-box;
    justify-content: space-between;

    .i {
        font-size: 1.625rem;
        color: gray;
    }

    // position: relative;
    width: 100%;
    height: 2.75rem;
    overflow: hidden;
    background-color: #fff;
    line-height: 2.75rem;
    text-align: center;
    font-size: 1.0625rem;
}

.date {
    height: 3.0625rem;
    width: 24.375rem;
    // background: 
}

.main {
    background: #fff;
}
.tab {
    display: inline-block;
    /* float: left; */
    font-size: .875rem;
    color: #191a1b;
    height: 3.0625rem;
    width: 50%;
    line-height: 3.0625rem;
    text-align: center;
    border-top: 1px solid #ededed;
    border-bottom: 1px solid #ededed;
}
</style>